import React, { Component } from 'react';
import './App.css'
import { HashRouter, Switch, Route, Link } from 'react-router-dom'
import Home from './components/Home';
import Error404 from './components/Error404';
import firebase from 'firebase'
import TagPage from './components/TagPage';
import Upload from './components/Upload';
import Search from './components/Search';
import MenuContainer from './container/Menu';
import Routes from './components/Routes';
import Edit from './components/Edit';

class App extends Component {
  constructor(){
    super()
    this.state = {
        tags: [],
        loading: true,
        shayariObject: {}
    }
  }

  componentDidMount() {
    var tagsArray = [];
    var tempShayariObject = {};
    var totalShayaris = 0;
    firebase.firestore().collection('tags').get()
    .then(snap => {
      snap.forEach(doc => {
        var tag =  doc.id;
        totalShayaris = doc.data().totalShayaris;
        tagsArray.push(tag)
        tempShayariObject[tag] = {
          titleArray: [],
          contentArray: [],
          poetArray: []
        }
      })
      tagsArray.forEach(tag => {
        tempShayariObject[tag].relatedTagsObject = {};
        for(let i=0; i<totalShayaris; i++){
          tempShayariObject[tag].relatedTagsObject[i] = [];
        }
      })
      this.setState(prev => ({
        tags: tagsArray,
        loading: false,
        shayariObject: Object.assign({}, prev.shayariObject, tempShayariObject)
      }))
    })
    .catch(error => {
      alert(error.message)
    })
  }

  putIntoShayariObject = (shayariObject) => {
    this.setState(prev => ({
      shayariObject: Object.assign(prev.shayariObject, shayariObject)
    }))
  }

  render() {

    return (
      this.state.loading ? <h1>loading</h1> :
      <div className="App">
      <HashRouter>
      <div 
      className='header'>
        <MenuContainer />
        <span className='headerName'><Link to='/' className='link'>ùì´ùìÆùìºùìΩùìºùì±ùì™ùîÇùì™ùìªùì≤ùìº.ùì¨ùì∏ùì∂</Link></span>
        <Search tags={this.state.tags} />
      </div>

      <Switch>
        <Route exact path='/' render={props => <Home tags={this.state.tags} />} />

        <Route path='/tags/:tag' render={props => 
        <TagPage 
        tag={props.match.params.tag}
        shayariObject={this.state.shayariObject}
        putIntoShayariObject={this.putIntoShayariObject} />} />

        <Route exact path='/upload' render={props => <Upload tags={this.state.tags} />} />
        <Route exact path='/edit' render={props => <Edit tags={this.state.tags} />} />

        <Route path='*' component={Error404} />
      </Switch>
      {/* <Routes tag={this.state.tags} shayariObject={this.state.shayariObject} putIntoShayariObject={this.putIntoShayariObject} /> */}
      </HashRouter>
    </div>
    )
  }
}

export default App;